.list__equip {
    background-image: url('../../../assets/img/background/bg-equip.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    padding: 60px 0;
}

.list__equip h2 {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    color: #020c16;
    text-align: center;
}

.list__equip--item {
    overflow: auto;
    max-height: 500px;
}

.list__equip--item button {
    margin-top: 1rem;
}

.list__equip--item .item {
    text-align: center;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 .5rem 3rem .5rem;
}


.list__equip--item .item.disabled,
.list__equip--item .item.removing {
    opacity: .7;
    cursor: not-allowed;
    pointer-events: none;
}

.list__equip--item .item img {
    width: 180px;
    height: 210px;
    object-fit: contain;
    cursor: pointer;
}


@media (min-width: 768px) {
    .list__equip .list__equip--item {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }

    .list__equip .list__equip--item .item {
        width: fit-content;
        flex-direction: column;
        display: flex;
        align-items: center;
    }
}